<script setup>
import { defineProps, ref, onActivated, defineEmits, watch } from 'vue'

const emit = defineEmits(['update:sliders'])

const props = defineProps(['series', 'sliders', 'characteristics']) // Recevoir les s√©ries et sliders via props
const similaritiesTable = ref([]) // Tableau des similarit√©s
const comparisonResult = ref(null) // R√©sultat de la comparaison entre deux s√©ries
let editFeature = ref(false) // √âtat pour afficher/masquer la personnalisation

// Copie locale pour l'√©dition
const localSliders = ref({ ...props.sliders })

// Quand on ouvre la personnalisation, synchroniser la copie locale
function openEdit() {
  localSliders.value = { ...props.sliders }
  editFeature.value = true
}

// √âmettre la mise √† jour au parent
function validerChanges() {
  emit('update:sliders', localSliders.value)
  editFeature.value = false
  // R√©initialiser les r√©sultats
  comparisonResult.value = null
  similaritiesTable.value = []
}
watch(
  () => props.sliders,
  () => {
    const selectedSeries = props.series.filter(serie => serie.checked)
    if (typeAffichage.value === 1 && selectedSeries.length > 0) {
      calculerSimilaritesPourUneSerie(selectedSeries[0].name)
    } else if (typeAffichage.value === 2 && selectedSeries.length > 1) {
      calculerSimilaritesEntreDeuxSeries(selectedSeries[0].name, selectedSeries[1].name)
    }
  },
  { deep: true }
)

// Fonction pour calculer la similarit√© entre deux vecteurs
function cosineSimilarity(A, B) {
  let dotproduct = 0;
  let mA = 0;
  let mB = 0;
  for (let i = 0; i < A.length; i++) {
    dotproduct += (A[i] * B[i]);
    mA += (A[i] * A[i]);
    mB += (B[i] * B[i]);
  }
  mA = Math.sqrt(mA);
  mB = Math.sqrt(mB);
  const denom = mA * mB;
  if (!denom) return 0;
  const result = dotproduct / denom;
  return isNaN(result) ? 0 : result;
}

// Fonction pour r√©cup√©rer les caract√©ristiques d'une s√©rie
function getFeatures(serieName, featureKeys) {
  const serie = props.characteristics.find(item => item.name === serieName)
  if (!serie) return featureKeys.map(() => [])

  // R√©cup√©rer les cl√©s de colonnes du CSV
  const allKeys = Object.keys(serie)

  // D√©finir les plages d'index pour chaque feature
  const llamaSynopsisCols = allKeys.slice(1, 51)
  const audioCols = allKeys.slice(51, 56)
  const videoCols = allKeys.slice(56)

  // Pour chaque feature, retourne le vecteur pond√©r√© par les sliders
  return featureKeys.flatMap(key => {
    if (key === 'llama_Synopsis') {
      return llamaSynopsisCols.map(col => (parseFloat(serie[col]) || 0) * (parseFloat(props.sliders[col]) || 0))
    }
    if (key === 'audio') {
      return audioCols.map(col => (parseFloat(serie[col]) || 0) * (parseFloat(props.sliders[col]) || 0))
    }
    if (key === 'vid√©o') {
      return videoCols.map(col => (parseFloat(serie[col]) || 0) * (parseFloat(props.sliders[col]) || 0))
    }
    return []
  })
}

// Fonction pour calculer les similarit√©s pour une s√©rie donn√©e
function calculerSimilaritesPourUneSerie(serie_name) {
  const selectedSerie = props.series.find(serie => serie.name === serie_name)
  if (!selectedSerie) return

  const featureKeys = ['llama_Synopsis', 'audio', 'vid√©o'] // Cl√©s des caract√©ristiques utilis√©es pour le calcul

  similaritiesTable.value = props.series
    .filter(serie => serie.name !== serie_name)
    .map(serie => {
      const featureSimilarities = featureKeys.map(key => {
        const weight = parseFloat(props.sliders[key]) || 0
        if (weight === 0) return null // Ignorer la feature si poids 0
        const featureVectorA = getFeatures(selectedSerie.name, [key])
        const featureVectorB = getFeatures(serie.name, [key])
        const similarity = cosineSimilarity(featureVectorA, featureVectorB)
        return {
          key,
          similarity,
          weight
        }
      }).filter(Boolean) // Retirer les null

      const weightedSimilarity = featureSimilarities.length > 0
        ? featureSimilarities.reduce(
            (sum, feature) => sum + feature.similarity * feature.weight,
            0
          ) / featureSimilarities.reduce((sum, feature) => sum + feature.weight, 0)
        : 0

      return {
        name: serie.name,
        similarity: weightedSimilarity,
        featureSimilarities: featureSimilarities,
        image: serie.image,
        description: serie.description
      }
    })
    

  similaritiesTable.value.sort((a, b) => b.similarity - a.similarity) // Trier par similarit√© d√©croissante
}

// Fonction pour calculer la similarit√© entre deux s√©ries
function calculerSimilaritesEntreDeuxSeries(serie1Name, serie2Name) {
  const featureKeys = ['llama_Synopsis', 'audio', 'vid√©o'] // Cl√©s des caract√©ristiques utilis√©es pour le calcul

  const featureSimilarities = featureKeys.map(key => {
    const weight = parseFloat(props.sliders[key]) || 0
    if (weight === 0) return null
    const featureVectorA = getFeatures(serie1Name, [key])
    const featureVectorB = getFeatures(serie2Name, [key])
    const similarity = cosineSimilarity(featureVectorA, featureVectorB)
    return {
      key,
      similarity,
      weight
    }
  }).filter(Boolean)

  const weightedSimilarity = featureSimilarities.length > 0
    ? featureSimilarities.reduce(
        (sum, feature) => sum + feature.similarity * feature.weight,
        0
      ) / featureSimilarities.reduce((sum, feature) => sum + feature.weight, 0)
    : 0

  // Stocker le r√©sultat dans comparisonResult
  comparisonResult.value = {
    serie1Name,
    serie2Name,
    weightedSimilarity,
    featureSimilarities
  }
}

// Charger les donn√©es et effectuer les calculs √† chaque fois que le composant est activ√©
let typeAffichage = ref(0) // 1 pour une s√©rie, 2 pour deux s√©ries
onActivated(async () => {
  try {
    console.log('Caract√©ristiques disponibles :', props.characteristics)
    // V√©rifier si la liste des s√©ries s√©lectionn√©es est vide
    const selectedSeries = props.series.filter(serie => serie.checked)
    if (selectedSeries.length === 0) {
      // Rediriger vers Home si aucune s√©rie n'est s√©lectionn√©e
      window.location.hash = '#/'
      return
    }
    console.log('S√©ries s√©lectionn√©es :', selectedSeries)
    console.log(selectedSeries.length)
    if (selectedSeries.length === 1) {
      typeAffichage.value = 1
      // Calculer les similarit√©s pour la premi√®re s√©rie coch√©e
      calculerSimilaritesPourUneSerie(selectedSeries[0].name)
      
    } else if (selectedSeries.length === 2) {
      // Calculer les similarit√©s entre deux s√©ries coch√©es
      calculerSimilaritesEntreDeuxSeries(selectedSeries[0].name, selectedSeries[1].name)
      typeAffichage.value = 2
    }
    
    /* teste pour faire un calcul sur toutes les s√©ries coch√©es
    else {
      // Calculer les similarit√©s pour toutes les s√©ries coch√©es
      props.series.forEach(serie => {
        if (serie.checked) {
          calculerSimilaritesPourUneSerie(serie.name)
        }
      })
    }
    */
  } catch (error) {
    console.error('Erreur lors du chargement des caract√©ristiques :', error)
  }
})

// Fonction pour afficher la similarit√© pour chaque feature
function showFeatureSimilarities(featureSimilarities) {
  const message = featureSimilarities
    .map(feature => `${feature.key} : ${(feature.similarity * 100).toFixed(2)}% (Pond√©ration : ${feature.weight})`)
    .join('\n')
  alert(`Similarit√© par feature :\n${message}`)
}
</script>

<template>
  <h2>R√©sultats des s√©ries s√©lectionn√©es</h2>
  <div id="personnalisation">
    <button @click="openEdit">personnaliser le resultat</button>
    <div v-if="editFeature">
      <h3>Personnalisation des crit√®res</h3>
      <ul>
        <li v-for="(value, key) in localSliders" :key="key">
          <label>
            <input
              type="checkbox"
              v-model="localSliders[key]"
              true-value="1"
              false-value="0"
            />
            {{ key }}
          </label>
        </li>
      </ul>
      <button @click="validerChanges">Valider les changements</button>
      <button @click="editFeature = false">Annuler</button>
    </div>
  </div>
  <table v-if="typeAffichage===1" class="similarities-table">
    <caption>Tableau des similarit√©s</caption>
    <thead>
      <tr>
        <th>S√©rie</th>
        <th>Image</th>
        <th>Description</th>
        <th>Score de Similarit√©</th>
        <th>Action</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in similaritiesTable" :key="item.name">
        <td>{{ item.name }}</td>
        <td>
          <img :src="item.image" alt="Image de la s√©rie" class="serie-image" />
        </td>
        <td>{{ item.description }}</td>
        <td>{{ (item.similarity * 100).toFixed(2) }}%</td>
        <td>
          <button @click="showFeatureSimilarities(item.featureSimilarities)">Voir Similarit√© par Feature</button>
        </td>
      </tr>
    </tbody>
  </table>

  <!-- Affichage des r√©sultats de la comparaison -->
  <div v-else-if="typeAffichage === 2 && comparisonResult" class="comparison-result">
    <h3>Comparaison entre {{ comparisonResult.serie1Name }} et {{ comparisonResult.serie2Name }}</h3>
    <p><strong>Similarit√© globale pond√©r√©e :</strong> {{ (comparisonResult.weightedSimilarity * 100).toFixed(2) }}%</p>
    <ul>
      <li v-for="feature in comparisonResult.featureSimilarities || []" :key="feature.key">
        <strong>{{ feature.key }} :</strong> {{ (feature.similarity * 100).toFixed(2) }}% (Pond√©ration : {{ feature.weight }})
      </li>
    </ul>
  </div>
  <p v-else>Aucune s√©rie trouv√©e.</p>
</template>

